@use 'sass:map';
@use '../function' as *;
@use '../mixin' as *;
@use '../config' as *;

@mixin generate-form-control(
  $selector,
  $has-states: false,
  $has-sizes: false,
  $has-select: true
) {
  #{$selector} {
    --webkit-date-line-height: 1.375;

    appearance: none;
    background-color: color(background, form);
    border: $form-control-border-width solid color(border, form);
    border-radius: $form-control-border-radius;
    box-sizing: border-box;
    color: color(text, form);
    display: block;
    font-size: $form-control-font-size;
    font-weight: $form-control-font-weight;
    line-height: $form-control-line-height;
    padding: $form-control-padding;
    transition-duration: transition(duration);
    transition-property: border, box-shadow;
    transition-timing-function: transition(timing-function);
    width: 100%;

    &::placeholder {
      color: color(placeholder, form);
    };

    &::-webkit-datetime-edit {
      line-height: var(--webkit-date-line-height);
    }

    &:focus {
      @include focus-ring(
        $type: $form-control-focus-ring-type,
        $border-color: color(border-focus, form),
        $ring-color: color(ring-focus, form),
        $box-shadow-type: $form-control-focus-ring-box-shadow-type,
        $ring-width: $form-control-focus-ring-width,
        $ring-offset: $form-control-focus-ring-offset
      );
    }

    &[type='color'] {
      height: $form-control-color-height;
      padding: $form-control-color-padding;

      &::-webkit-color-swatch-wrapper {
        padding: 0;
      }

      &::-moz-color-swatch {
        border: 0;
        border-radius: $form-control-border-radius;
      }

      &::-webkit-color-swatch {
        border: 0;
        border-radius: $form-control-border-radius;
      }
    }

    &[disabled],
    &[disabled='true'] {
      @include field-disabled(
        $background: color(background-disabled, form),
        $border: color(border-disabled, form)
      );
    }

    // Set height to textarea
    @at-root {
      textarea#{$selector} {
        height: $form-control-textarea-height;
        min-height: $form-control-textarea-height;
        resize: vertical;
      }
    }

    // Validation states
    @if ($has-states) {
      &--valid,
      &--invalid {
        background-position: center right $select-icon-right-offset;
        background-repeat: no-repeat;
        background-size: $select-icon-width auto;
        padding-inline-end: $select-padding-right;

        html[dir='rtl'] & {
          background-position: center left $select-icon-right-offset;
        }
      }

      &--valid {
        @include field-icon($form-valid-icon, color(success, alert, true));
        border-color: color(success, alert);

        &:focus {
          @include focus-ring(
            $type: $form-control-focus-ring-type,
            $border-color: color(valid, form),
            $ring-color: color(valid-focus-ring, form),
            $box-shadow-type: $form-control-focus-ring-box-shadow-type,
            $ring-width: $form-control-focus-ring-width,
            $ring-offset: $form-control-focus-ring-offset
          );
        }
      }

      &--invalid {
        @include field-icon($form-invalid-icon, color(danger, alert, true));
        border-color: color(danger, alert);

        &:focus {
          @include focus-ring(
            $type: $form-control-focus-ring-type,
            $border-color: color(invalid, form),
            $ring-color: color(invalid-focus-ring, form),
            $box-shadow-type: $form-control-focus-ring-box-shadow-type,
            $ring-width: $form-control-focus-ring-width,
            $ring-offset: $form-control-focus-ring-offset
          );
        }
      }
    }

    // Sizes
    @if ($has-sizes) {
      &--sm {
        --webkit-date-line-height: 1.36;

        font-size: $form-control-font-size-sm;
        padding: $form-control-padding-sm;

        &[type='color'] {
          height: $form-control-color-height-sm;
          padding: $form-control-color-padding-sm;
        }
      }

      &--lg {
        --webkit-date-line-height: 1.387;

        font-size: $form-control-font-size-lg;
        padding: $form-control-padding-lg;

        &[type='color'] {
          height: $form-control-color-height-lg;
          padding: $form-control-color-padding-lg;
        }
      }
    }
  }

  // Generate select styling
  @if ($has-select) {
    select#{$selector} {
      &:not([multiple]):not([size]) {
        @include field-icon($select-icon, color(select-foreground, form, true));
        background-position: center right $select-icon-right-offset;
        background-repeat: no-repeat;
        background-size: $select-icon-width auto;
        padding-inline-end: $select-padding-right;

        html[dir='rtl'] & {
          background-position: center left $select-icon-right-offset;
        }
      }
    }
  }
}
