@use 'sass:map';
@use 'typography' as *;
@use 'spacer' as *;
@use 'display' as *;

$form-label-font-size: null !default;
$form-label-font-size-sm: $font-size-sm !default;
$form-label-font-size-lg: $font-size-lg !default;

$form-label-font-style: null !default;
$form-label-font-weight: null !default;

$fieldset-gap-size: map.get($spacers, s) !default;

$legend-font-size: clamp(#{map.get($font-sizes, 'h5')}, 5vw, #{map.get($font-sizes, 'h4')}) !default;
$legend-font-weight: 700 !default;

$form-description-font-size: null !default;
$form-description-font-style: null !default;
$form-description-font-weight: null !default;

$form-row-col-width: 20ch !default;

$form-control-font-size: $font-size-base !default;
$form-control-font-size-sm: $font-size-sm !default;
$form-control-font-size-lg: $font-size-lg !default;

$form-control-font-weight: null !default;
$form-control-line-height: 1.5 !default;
$form-control-padding: 0.5em 0.75em !default;
$form-control-padding-sm: 0.25em 0.5em !default;
$form-control-padding-lg: 0.65em 1em !default;
$form-control-border-width: 1px !default;
$form-control-border-radius: $border-radius-sm !default;

$form-control-focus-ring-type: box-shadow !default;
$form-control-focus-ring-box-shadow-type: outside !default;
$form-control-focus-ring-width: 0.25rem !default;
$form-control-focus-ring-offset: 2px !default;

$form-control-color-padding: 0.5em !default;
$form-control-color-padding-sm: 0.25em !default;
$form-control-color-padding-lg: 0.75em !default;
$form-control-color-height: calc(#{$form-control-line-height}em + ( #{$form-control-color-padding} + #{$form-control-border-width} ) * 2) !default;
$form-control-color-height-sm: calc(#{$form-control-line-height}em + ( #{$form-control-color-padding-sm} + #{$form-control-border-width} ) * 2) !default;
$form-control-color-height-lg: calc(#{$form-control-line-height}em + ( #{$form-control-color-padding-lg} + #{$form-control-border-width} ) * 2) !default;

$form-control-textarea-height: 6rem !default;

$form-check-border-width: 1px !default;
$form-check-checkbox-border-radius: $border-radius-sm !default;
$form-check-font-size: 1.125rem !default;
$form-check-font-size-sm: $font-size-base !default;
$form-check-font-size-lg: $font-size-lg !default;
$form-check-font-weight: $form-label-font-weight !default;
$form-check-line-height: $line-height-heading !default;
$form-check-margin-block-start: 0.1em !default;
$form-check-vertical-alignment: center !default;

$form-check-focus-ring-type: outline !default;
$form-check-focus-ring-box-shadow-type: outside !default;
$form-check-focus-ring-width: 2px !default;
$form-check-focus-ring-offset: 2px !default;

$form-switch-border-width: 1px !default;
$form-switch-font-size: 1.125rem !default;
$form-switch-font-size-sm: $font-size-base !default;
$form-switch-font-size-lg: $font-size-lg !default;
$form-switch-font-weight: $form-label-font-weight !default;
$form-switch-line-height: $line-height-heading !default;
$form-switch-margin-block-start: 0.25em !default;

$form-file-background: primary !default;

$form-range-track-height: 0.25rem !default;
$form-range-track-broder-radius: 0.15rem !default;
$form-range-thumb-height: 1rem !default;
$form-range-thumb-width: 1rem !default;
$form-range-thumb-broder-radius: 0.5rem !default;

$form-range-focus-ring-type: outline !default;
$form-range-focus-ring-box-shadow-type: outside !default;
$form-range-focus-ring-width: 2px !default;
$form-range-focus-ring-offset: 2px !default;

$select-icon-width: 1.25em !default;
$select-icon-right-offset: 0.5em !default;
$select-padding-right: calc(0.75em + $select-icon-width) !default;
