@use '../function' as *;
@use '../mixin' as *;
@use '../config' as *;

@mixin generate-typography {
  // Basics
  html {
    -webkit-tap-highlight-color: hsl(0 0% 0% / 0);
  }

  body {
    font-family: $font-family-base;
    font-size: $font-size-base;
    font-weight: $font-weight-base;
    line-height: $line-height-base;
  }

  @if setting(hyphens) {
    p,
    li,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      hyphens: auto;
      overflow-wrap: break-word;
    }
  }

  // Headings
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: color(heading);
    font-family: $font-family-heading;
    font-weight: $heading-font-weight;
    line-height: $line-height-heading;
  }

  h1,
  .h1 {
    font-size: font-size(h1);
  }

  h2,
  .h2 {
    font-size: font-size(h2);
  }

  h3,
  .h3 {
    font-size: font-size(h3);
  }

  h4,
  .h4 {
    font-size: font-size(h4);
  }

  h5,
  .h5 {
    font-size: font-size(h5);
  }

  h6,
  .h6 {
    font-size: font-size(h6);
  }

  // Lists
  ul,
  ol {
    @include layout-stack(xxs, $width: false);
    list-style-position: inside;

    li {
      list-style-position: outside;

      &::marker {
        color: color(marker);
      }
    }
  }

  li > ul,
  li > ol {
    margin-block-start: spacer(xxs);
  }

  dl {
    dt {
      color: color(heading);
      font-weight: bold;
    }

    dd {
      margin: 0;
    }

    dd + dt {
      margin-block-start: spacer(s);
    }
  }

  // Quote, blockquote
  .quote {
    @include layout-stack(xs);
    border-inline-start: 0.5rem solid color(blockquote-border);
    padding-inline-start: spacer(m);

    blockquote {
      border-inline-start: 0;
      padding-inline-start: 0;
    }

    figcaption {
      text-align: start;
    }
  }

  blockquote {
    @include layout-stack(xs);
    border-inline-start: 0.5rem solid color(blockquote-border);
    margin-inline-start: 0;
    padding-inline-start: spacer(m);
  }

  // Inline elmeents
  abbr[title] {
    border-block-end: 1px dotted;
    cursor: help;
    text-decoration: none;
  }

  mark {
    background-color: color(mark-background);
    border-radius: $inline-text-element-border-radius;
    color: color(mark-foreground);
    padding: $inline-text-element-padding;
  }

  code,
  kbd,
  samp {
    background-color: color(code-background);
    border-radius: $inline-text-element-border-radius;
    color: color(code-foreground);
    padding: $inline-text-element-padding;
  }

  // Lead
  .lead {
    font-size: $font-size-lead;
  }
}
