@use 'sass:map';
@use '../function' as *;
@use '../config' as *;

@mixin generate-color-variables(
  $colors: $colors,
  $selector: ':root'
) {
  @each $key, $value in $colors {
    #{$selector} {
      @each $name, $color in $value {
        @if $color {
          --#{$internal-prefix}#{$key}-color-#{$name}: #{$color};
        }
      }
    }
  }
}
